@import url(https://fonts.googleapis.com/css?family=Petit+Formal+Script);
@import url(https://use.typekit.net/dns6ten.css);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed);
@import url(https://fonts.googleapis.com/css?family=Montserrat|Roboto+Slab);
html {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  font-size: 16px;
}
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  background: url(/static/media/background.fa663b07.png) no-repeat center left;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;

  background-attachment: fixed;
  background-repeat: no-repeat;
  overflow-x: hidden;
}

@media only screen and (max-width: 1000px) {
  body {
    max-width: 1000px;
  }
}

.outline-section {
  /* border: 2px solid #635F5F; */
  width: 90vw;
  max-width: 1100px;
  margin: 0 auto;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  align-content: center;
  justify-content: center;
}

.outer-rectangle {
  background: rgba(218, 216, 215, 0.849);
  padding: 0px 0px 20px 0px;
  width: 100%;
  height: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* align-content: center; */
}

.inner-rectangle {
  background: #ffffff;
  margin: 0 auto;
  width: 100%;
  height: 90%;
}

h3 {
  font-family: Roboto Condensed;

  font-style: normal;
  font-weight: normal;
  font-size: 1.5rem;
  letter-spacing: 0.03em;
  color: rgb(110, 103, 102);
  width: 95%;
  text-align: left;
  padding: 10px 10px;

  /*
align-self: flex-end;
justify-self: flex-end; */
}

#about,
#work {
  height: 70px;
}

#home {
  height: 100vh;
}

.second-navbar {
  position: relative;
  top: 5%;
}

.second-navbar ul {
  text-align: right;
  margin-right: 45px;

  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 1.325rem;
}
.second-navbar ul li {
  display: inline;
  margin: 15px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  padding: 7px;
}

.second-navbar ul li a {
  color: #635f5f;
  text-decoration: none;
}

.second-navbar ul li a:hover {
  border-bottom: 2px solid #9d9391;
}

.name-intro {
  background: #e9d9d4e3;
  border-radius: 100%;
  margin: 0 auto;
  width: 500px;
  height: 500px;

  position: relative;
  top: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex-direction: column;
}
header h1 {
  font-family: Petit Formal Script;
  font-style: normal;
  font-weight: normal;
  font-size: 5rem;
  text-align: center;
  letter-spacing: -0.07em;
  color: #ffffff;
  text-shadow: 3px 2px 1px rgba(0, 0, 0, 0.42);
  /* transform: rotate(-8deg); */
  width: 100%;
  margin: 0 auto;
  /* border: 1px solid black; */
}

header h2 {
  padding-top: 20px;
  font-family: Roboto Condensed;
  font-style: normal;
  line-height: 23px;
  font-size: 3rem;
  text-align: center;
  letter-spacing: -0.01em;
  color: #494646;
  margin-top: 50px;
}

.intro-section {
  height: 100vh;
}

@media only screen and (max-width: 520px) {
  @media screen and (min-width: 150px) {
    .name-intro {
      top: 20%;
      width: 300px;
      height: 300px;
    }

    header h1 {
      font-size: 3.5rem;
      text-align: center;
    }

    header h2 {
      padding-top: 10px;
      font-size: 2rem;
    }
  }

  @media screen and (min-width: 420px) {
