@media only screen and (max-width: 400px) {
  .second-navbar ul {
    text-align: center;
    margin-right: 0px;
    font-size: 1.225rem;
  }

  .second-navbar ul li {
    margin: 8px;
    padding: 7px;
  }
}

.aboutMe {
  display: flex;
  flex-direction: row;
  width: 95%;
  min-height: 80vh;
  height: 100%;
  margin: 0 auto;
}

.about-name {
  width: 30%;
  margin-top: 5%;
  margin-left: 5%;
}
.about-description {
  width: 60%;
  margin-left: 50px;
  margin-top: 5%;
}

.about-description h4,
.about-name h4 {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 2.65rem;
  text-align: left;
  letter-spacing: -0.01em;
  color: #494646;
  color: #c59795;
}

.about-name .bio-paragraph {
  font-family: Roboto Condensed;
  letter-spacing: 1px;
  font-size: 1.3rem;
  line-height: 2em;
  text-align: left;
  color: #494646;
}

.about-name ul {
  font-family: Roboto Condensed;
  letter-spacing: 1px;
  font-size: 1.1rem;
  line-height: 2em;
  text-align: left;
  color: #494646;
  margin-top: 22px;
}

.about-name ul br {
  display: none;
}

.about-description .bio-paragraph {
  font-family: Roboto Condensed;
  letter-spacing: 0px;
  font-size: 1.2rem;
  line-height: 2em;
  text-align: left;
  color: #494646;
}

.about-description .bio-tools {
  font-family: Roboto Condensed;
  letter-spacing: 1px;
  font-size: 1.1rem;
  line-height: 2em;
  text-align: left;
  color: #494646;
}

.about-description .bio-tools p {
  margin-bottom: 3%;
}

.title {
  border-bottom: 1px solid rgba(196, 192, 190, 0.897);
  display: block;
  width: 75%;
}

.bio-tools strong {
  font-weight: bold;
}

.tl-blt {
  font-weight: bold;
  font-size: 1.5rem;
}

.bio-paragraph-about {
  border-bottom: 1px solid rgba(196, 192, 190, 0.897);
  display: block;
  width: 90%;
}

@media only screen and (max-width: 660px) {
  .aboutMe {
    width: 90%;
    height: 100%;
    flex-direction: column;
  }

  .about-name {
    width: 100%;
    margin-left: 0%;
  }

  .about-name ul li {
    display: inline;
    margin: 0px 10px;
    font-size: 1rem;
    font-weight: bold;
  }

  .about-description .bio-paragraph {
    margin-bottom: 20px;
    width: 100%;
    font-size: 1.1rem;
  }

  .about-description {
    width: 100%;
    margin-left: 0px;
    margin-bottom: 20px;
  }

  .about-name ul br {
    display: inline-block;
  }

  .tl-blt {
    font-weight: bold;
    font-size: 1.2rem;
  }

  .about-description .bio-tools {
    font-family: Roboto Condensed;
    letter-spacing: 1px;
    font-size: 1.1rem;
    text-align: left;
    color: #494646;
  }

  .title {
    width: 100%;
  }
}

.work-section {
  height: 100vh;
  margin-bottom: 30px;
}

.project {
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 100%;
}

.project-info {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.project-text {
  width: 100%;

  height: 70%;
}

.project-header {
  width: 100%;
}

.project-img {
  width: 50%;

  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 20px;
}

.project-img img {
  height: 90%;
  width: 100%;
  max-width: 650px;
  object-fit: contain;
}

.project-text {
  text-align: center;
}
.project button {
  background: #91867c;
  text-align: center;
  width: 30%;
  max-width: 150px;
  min-width: 85px;
  height: 42px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-top: 3%;
  text-align: center;
}

.project button a {
  font-family: Montserrat;
  font-style: normal;
  font-weight: normal;
  line-height: normal;
  font-size: 1rem;
  text-align: center;
  text-decoration: none;
  color: #ffffff;
  display: block;
  width: 100%;
  height: 100%;
  padding-top: 10px;
}

.project button:hover {
  background: #e9d9d4e1;
}

.project button:hover a {
  color: rgb(110, 103, 102);
  text-decoration: none;
  font-weight: bold;
}

.project-text p {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  line-height: 25px;
  font-size: 1rem;
  letter-spacing: 0.03em;
  text-align: left;
  color: #9d9391;
  color: #5a5252;
  padding: 10px 20px;
}

.project-header h4 {
  font-family: Roboto Slab;
  font-style: normal;
  font-weight: normal;
  font-size: 3rem;
  letter-spacing: 0.03em;
  color: #9d9391;
  text-align: left;
  padding: 10px 36px;
}

.github-link {
  display: block;
  text-align: left;
  text-decoration: none;
  padding-left: 15px;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  color: rgb(110, 103, 102);
  margin-top: 10px;
}

.github-link .react-icons {
  font-size: 23px;
  color: rgb(110, 103, 102);
  margin-left: 10px;
  vertical-align: middle;
  padding-bottom: 1px;
}

.github-link:hover {
  text-decoration: underline;
}

.tech-stack {
  display: block;
  margin-top: 10px;
}

.tech-stack strong {
  font-weight: bold;
}

.back-end {
  display: block;
  margin-top: 10px;
}

@media only screen and (max-width: 660px) {
  .project {
    flex-direction: column;
  }

  .project-info {
    width: 100%;
    height: 50%;
  }
  .project-header {
    width: 100%;
    margin-top: 15px;
  }

  .project-header h4 {
