      font-size: 15px;
    }
  }
}

@media only screen and (min-width: 850px) {
  .work-section {
    height: 90vh;
    margin-bottom: 30px;
  }
}
@media only screen and (min-width: 1100px) {
  .name-span {
    display: block;
  }
}

#navbar {
  -webkit-transform: translateY(-150px);
          transform: translateY(-150px);
  -webkit-transition: -webkit-transform 400ms ease-in;
  transition: -webkit-transform 400ms ease-in;
  transition: transform 400ms ease-in;
  transition: transform 400ms ease-in, -webkit-transform 400ms ease-in;

  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 5;
  background: #e9d9d4e1;
  height: 50px;
  border-bottom: 1px solid black;
}

#navbar.slideIn {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

#navbar.slideOut {
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
}

#navbar ul {
  text-align: right;
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  font-size: 1.325rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  height: 50px;
  margin-right: 9%;
}

#navbar ul li {
  display: inline;
  margin: 15px;
}

#navbar ul li a {
  color: rgb(110, 103, 102);
  text-decoration: none;
}

#navbar ul li a:hover {
  border-bottom: 2px solid rgb(110, 103, 102);
}

@media only screen and (max-width: 760px) {
  #navbar ul {
    text-align: center;
    font-size: 1.3rem;
    align-items: center;
    justify-content: center;
    margin-right: 0px;
  }
}

.contact-ftr {
  background: rgba(218, 216, 215, 0.938);
  padding: 0px 0px 20px 0px;
  width: 95vw;
  max-width: 1100px;
  height: 40vh;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  padding: 40px 50px;
  box-sizing: border-box;

  -ms-box-orient: horizontal;
  display: -moz-flex;
  display: flex;
  justify-content: space-around;
  flex-flow: row wrap;
  align-items: stretch;
}

.contact,
.connect {
  width: 50%;
}

.connect {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
  /* border: 1px solid black; */
}

.connect-buttons {
  display: grid;
  grid-template-columns: auto auto;
  align-content: center;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
}

.contact-info a,
.connect-buttons a {
  font-family: Roboto Condensed;
  font-size: 25px;
}
.contact-info a {
  margin-top: 10px;
  color: rgb(110, 103, 102);
  text-decoration: none;
  display: block;
  background: none;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
}
.connect-buttons a {
  margin-top: 10px;
  color: rgb(110, 103, 102);
  text-decoration: none;
  border: 2px solid rgb(110, 103, 102);
  padding: 0px 5px;
  width: 90%;
  max-width: 250px;
  height: 88px;
  display: block;
  background: none;
  box-sizing: border-box;
}

.connect-buttons a:hover {
  color: #fff;
  background: rgb(109, 107, 106);
  border: 2px solid rgba(110, 103, 102, 0.623);
  box-sizing: border-box;
}

.contact-info a:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.connect-buttons a:hover p {
  color: #fff;
}

.connect-buttons p {
  vertical-align: middle;
  position: relative;
  font-style: normal;
  font-weight: normal;
  top: 30%;
}

.connect-buttons a:hover .react-icons {
  color: #fff;
}
.contact-info p .react-icons {
  /* color: rgba(110, 103, 102, 0.801); */
  /* font-size: 25px; */
  /* margin-right: 10px; */
  /* min-height: 10px;
  min-width: 10px;
  display: block;
  border: 1px solid white; */
}
.email {
  color: rgba(110, 103, 102, 0.801);
  min-height: 10px;
  min-width: 10px;
  display: block;
}

.connect-buttons p .react-icons {
  color: rgba(110, 103, 102, 0.801);
  font-size: 25px;
  margin-left: 10px;
  display: block;
}

.contact p {
  font-family: Roboto Condensed;

  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  letter-spacing: 0.03em;
  color: rgb(110, 103, 102);
  text-align: left;
}

.contact .contact-info {
  width: 70%;
  height: 100%;
  margin: 0 auto;
  text-align: left;
}

.contact .contact-info h3 {
  font-size: 45px;
  margin-top: 5px;
  font-weight: bold;
  padding: 0px;
}

.contact .contact-info h4 {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  letter-spacing: 0.03em;
  color: rgb(110, 103, 102);
  margin-top: 30px;
}

.connect-buttons a p {
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
}

.signature {
  font-family: Roboto Condensed;
  font-style: normal;
  font-weight: normal;
  color: #916e6d;
  text-align: right;
  box-sizing: border-box;

  flex: 1 1 100%;
  padding: 20px 25px;
}

